import * as React from 'react';
import './App.css';
import Nav from './components/navBar/Nav';
import Item from './components/itemGrid/Item';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import ShoppingCartIcon from './components/cardImage/images/shopping-cart.png';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import { useState, useEffect } from "react";
import laranja from './components/cardImage/images/laranja.png';
import maca from './components/cardImage/images/maca.png';
import banana from './components/cardImage/images/banana.png';
import melancia from './components/cardImage/images/melancia.png';
import { env } from './env'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '5px solid rgba(224, 224, 224, 0.555);',
  boxShadow: 24,
  p: 4,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
};

function App() {

  const [APIData, setAPIData] = useState({})
  const [createModal, setCreateModal] = React.useState(true);
  const [openSucess, setOpenSucess] = React.useState(false);
  const [openError, setOpenError] = React.useState(false);

  const getAPI = async () => {

    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    const raw = JSON.stringify([
      {   "nome":"Oranges",
      "preco":1.99
      },
      {   "nome":"Apples",
          "preco":2.97
      },
      {   "nome":"Bananas",
          "preco":2.99
      },
      {   "nome":"Watermelon",
          "preco":3.99
      }
    ]);
    
    const requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow'
    };

    const response = await fetch(`${env.REACT_APP_API_URL}/processcart`, requestOptions); 
    const data = await response.json()
    
    setAPIData(data);
    console.log(data);
    
    setCreateModal(false);
    setOpenSucess(true);
    
  }


  const handleCloseSucess = () => setOpenSucess(false);

  const handleCloseError = () => setOpenError(false);
  

  return (
    <div className="App">
      <Nav />

      <h1 id="heading">Fast Track shop cart</h1>
      <div className="items">

        <div className="bag">
          <h1>Your Bag</h1>
          <p> 5 Items</p>
        </div>

        <div className="continue">
          <p>Continue Shopping</p>
        </div>

      </div>
      <div className="cardBuy">
        <div className="gridCard">
          <p>Product</p>
          <p>Price</p>
          <p>Quantity</p>
          <p>Total</p>
        </div>

        <Item
          image={laranja}
          nameItem={"Orange"}
          price={"1.99"}
          quality={"1"}
          total={"1.99"}
        />

        <Item
          image={maca}
          nameItem={"Apple"}
          price={"0.99"}
          quality={"3"}
          total={"2.97"}
        />

        <Item
          image={banana}
          nameItem={"Banana"}
          price={"2.99"}
          quality={"1"}
          total={"2.99"}
        />

        <Item
          image={melancia}
          nameItem={"Watermelon"}
          price={"3.99"}
          quality={"1"}
          total={"3.99"}
        />

       <div>
        <p> <b>Total: </b> 11.94 </p>
      </div> 

      </div>
      <Stack spacing={5} direction="row">
        <Button variant="contained" size="large" id="buyButton" onClick={getAPI}>
          <img src={ShoppingCartIcon} alt="cart" id="cart" /> Buy

        </Button>
      </Stack>

      {createModal ? (
       ""
      ) : (
        <Modal className="modalCard"
        open={openSucess}
        onClose={handleCloseSucess}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" component="h1" className='modalInfos'>
            <h2>Approved</h2>
            You buy {APIData[0].nome}, {APIData[1].nome}, {APIData[2].nome} and {APIData[3].nome}
            <br></br>
            <br></br>
            <div>
              <b>Total:</b> 11.94
            </div>
             
          </Typography>
        </Box>
      </Modal>
      )}

      

      <Modal
        open={openError}
        onClose={handleCloseError}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Error
          </Typography>

        </Box>
      </Modal>

    </div>
  );
}

export default App;
