import * as React from 'react';

export default function Item({ image, nameItem, price, quality, total }) {
  return (
    <div className="cardItem">
        <div className="item imageName">
            <img src={image} alt="" />
            <p>{nameItem}</p>
        </div>
        
        <div className="item">
            <p>{price}</p>
        </div>

        <div className="item">
            <p>{quality}</p>
        </div>

        <div className="item">
            <p>{total}</p>
        </div>
    </div>
  );
}